/* ==================== Media queries ======================= */

/*==========  Desktop First Method  ==========*/

/* Large Devices, Wide Screens */
@media only screen and (max-width : 1200px) {

}

/* Medium Devices, Desktops */
@media only screen and (max-width : 992px) {
    .header-logo {
        width: 15%;
    }

    .header-contacts a {
        font-size: 13px;
    }

    .header-contacts a:before {
        margin-right: 10px;
    }

    .header-nav__links a {
        font-size: 15px;
    }
    .services-list {
       justify-content: space-around;
    }
    .service-item {
        flex-basis: 40%;
    }
    .news-section:after {
        opacity: .4;
    }
}

@media only screen and (max-width : 868px) {
    .footer-top {
        flex-wrap: wrap;
    }

    .footer-logo {
        max-width: 100%;
        width: 100%;
        display: flex;
        align-items: center;
        margin-bottom: 20px;
        p {
            padding: 0 20px;
        }
    }
}

/* Small Devices, Tablets */
@media only screen and (max-width : 767px) {
    .header-content .wrapper {
        padding-top: 2%;
    }

    .header-nav {
        .nav-links {
            display: none;
        }
    }
    
    .mob-nav__btn {
        display: block;
    }

    // .header-top__contacts {
    //     justify-content: center;
    // }

    .header-contacts {
        //width: 100%;
        //text-align: center;
        margin-bottom: 20px;
    }
    
    .header-social {
        width: 100%;
        margin-bottom: 20px;
    }

    .header-social li {
        margin: 0 2px;
    }

    .header-contacts li {
        margin: 0 15px;
    }

    .slider-controls {
        display: none;
    }

    .service-item {
        flex-basis: 100%;
    }

    .news-item {
        flex-wrap: wrap;
    }

    .footer-nav,
    .footer-contacts {
        flex-grow: 1;
    }
    .footer-nav {
        max-width: 100%;
        width: 100%;
        justify-content: center;
        margin-bottom: 35px;
    }
    .footer-nav__links {
        text-align: center;
    }
    .footer-contacts {
        max-width: 100%;
        width: 100%;
        text-align: left;
    }

    .footer-bottom {
        flex-wrap: wrap;
        justify-content: center;
    }

    .footer-social {
        margin-bottom: 20px;
    }

    .empty {
        display: none;
    }
}

@media only screen and (max-width : 700px) {
    .header-contacts {
        flex-wrap: wrap;
        width: auto;
        text-align: left;
        padding: 0 20px;
        li {
            display: block;
            width: 100%;
        }
    }
    .header-top__contacts {
        flex-wrap: nowrap;
    }
    .header-social {
        width: auto;
    }
}


/* Extra Small Devices, Phones */
@media only screen and (max-width : 480px) {
    .header-top__contacts {
        .header-contacts {
            display: none;
        }
    }
    .footer-logo .logo {
        width: 100px;
        height: 100px;
    }
}

